/* major breakpoints */
@custom-media --sp (max-width: 767px);
@custom-media --tb (min-width: 768px) and (max-width: 1023px);
@custom-media --tb-h (min-width: 1024px) and (max-width: 1279px);
@custom-media --not-sp (min-width: 768px);
:root {
  --app-height: 100%;
}

@media screen and (min-width: 769px) {
  .profile dl {
    margin-bottom: 32px;
  }
}
@media screen and (max-width: 768px) {
  .profile dl {
    margin-top: 32px;
  }
}
.profile dl dt {
  color: #7A7676;
  font-size: 11px;
  font-family: "Fragment Mono", monospace;
  margin-bottom: 12px;
}
.profile dl dd {
  color: #16161A;
  font-size: 13px;
  font-family: "Selecta";
  font-weight: 400;
}

.next {
  background-color: #16161A;
}
.next dl dt {
  font-size: 11px;
  color: #D1C9C9;
  font-family: "Fragment Mono", monospace;
}
.next dl dd {
  margin-top: 12px;
  font-size: 13px;
  font-family: "Selecta";
  color: #FFFFFF;
}

.next-post {
  background-color: #16161A;
  transition-property: background-color;
  transition-duration: 0.3s;
}
.next-post h2 {
  color: #FFFFFF;
  transition-property: color;
  transition-duration: 0.3s;
}
.next-post.-white {
  background-color: #FFFFFF;
}
.next-post.-white h2 {
  color: #16161A;
}

footer {
  display: none !important;
}